import React, { useEffect } from "react"
import Layout from "../components/layout"

const updateBodyOpacity = (opacity) => {
  if(opacity < 1) {
    document.body.style.opacity = opacity;
    opacity += .01;
    setTimeout(() => updateBodyOpacity(opacity), 5);
    return;
  }

  updateHeroTextOpacity(0.0, 'hero1')
}

const updateHeroTextOpacity = (opacity, elementId) => {
  if(elementId === 'hero1' && opacity.toFixed(2) == .25) {
    updateHeroTextOpacity(0.0, 'hero2');
  }

  if(elementId === 'hero1' && opacity.toFixed(2) == .5) {
    updateHeroTextOpacity(0.0, 'hero3');
  }

  if(opacity < 1) {
    document.getElementById(elementId).style.opacity = opacity;
    if(elementId === 'hero3') {
      document.getElementById('resume').style.opacity = opacity;
      document.getElementById('judo').style.opacity = opacity;
    }
    opacity += .01;
    setTimeout(() => updateHeroTextOpacity(opacity, elementId), 10)
  }
};

const IndexPage = () => {
    useEffect(() => {
      let opacity = 0.0;
      updateBodyOpacity(opacity);
    }, []);

    return <Layout />;
};

export default IndexPage
